// https://webpack.docschina.org/guides/dependency-management/#requirecontext
// 通过require.context() 函数来常见自己的context

// 1.导入所有的svg图标
// 2.完成SvgIcon的全局注册
import SvgIcon from '@/components/SvgIcon'

const svgRequire = require.context('./svg', false, /\.svg/)
// 此时返回了一个Require函数,可以接收一个request的参数,用于require的导入
// 该函数提供了三个属性,可以通过svgRequire.keys()获得多有的svg图标
// 遍历图标,把图标作为require参数参到svgRequire导入函数中,完成本地svg图标的调入

svgRequire.keys().forEach((svgItem) => svgRequire(svgItem))

export default (app) => {
  app.component('svg-icon', SvgIcon)
}
