import path from 'path'

/**
 * 所有的子级路由
 */
const getChildrenRoutes = (routes) => {
  const result = []
  routes.forEach((route) => {
    if (route.children && route.children.length > 0) {
      result.push(...route.children)
    }
  })
  return result
}

/**
 * 处理脱离层级的路由  某个一级路由为其他子路由,则剔除该一级路由,保留路由层级
 */
export const filterRouters = (routes) => {
  // 所有的子集路由
  const childrenRoutes = getChildrenRoutes(routes)
  // 根据子集路由进行查重操作
  return routes.filter((route) => {
    // 根据route在childrenRoutes中进行查重,把重复路由表剔除
    return !childrenRoutes.find((childrenRoute) => {
      return childrenRoute.path === route.path
    })
  })
}

/**
 * 判断数据是否为空值
 */
function isNull(data) {
  if (!data) return true
  if (JSON.stringify(data) === '{}') return true
  if (JSON.stringify(data) === '[]') return true
  return false
}

/**
 * 根据routes数据(filterRouters),返回对应的menu规则数据
 */
export const generateMenus = (routes, basePath = '') => {
  const result = []
  // 不满足该条件`meta && meta.title && meta.icon`的数据不应该存在
  routes.forEach((item) => {
    // 不存在children && 不存在meta 直接renturn
    if (isNull(item.meta) && isNull(item.children)) return
    // 存在children,不存在meta,迭代generateMenus
    if (isNull(item.meta) && !isNull(item.children)) {
      result.push(...generateMenus(item.children))
      return
    }
    // 不存在children,存在meta || 存在children && 存在meta
    // 因为最终的menu需要进行跳转,此时我们需要合并path
    const routePath = path.resolve(basePath, item.path)
    // 路由分离之后,可能存在同名父级路由的情况
    let route = result.find((item) => item.path === routePath)
    // 当前路由尚未加入到result
    if (!route) {
      route = {
        ...item,
        path: routePath,
        children: []
      }

      // icon与title必须全部存在
      if (route.meta.icon && route.meta.title) {
        // meta存在生成route对象,放入arr
        result.push(route)
      }
    }

    // 存在children && 存在meta
    if (!isNull(item.children)) {
      route.children.push(...generateMenus(item.children, route.path))
    }
  })

  return result
}
